<template>
  <div class="farm-record">
    <ul class="m-folder-list">
      <template v-for="(list,pid) in recordStore" :key="pid">
        <li v-if="list" class="m-folder-item">
          <div class="main-info flex jc-between ai-center" @click="toggleOpened(pid)">
          <span class="c1">
            {{ language.type }} ( LP )<br>
            <b class="text">{{ farmStore[pid].farmPair }}</b>
          </span>
            <span class="c2">
            {{ language.tip_4_6 }}<br>
            <b class="green">{{ farmStore[pid].all_profit }}</b>
          </span>
            <span class="c3">
            {{ language.tip_4_7 }}<br>
            <b class="text">{{ farmStore[pid].rewardAmount }}</b>
          </span>
            <img v-if="openFlag[pid]" class="icon arrow" src="../../images/option/order1.png" alt=""/>
            <img v-else class="icon arrow" src="../../images/option/order.png" alt=""/>
          </div>
          <div class="detail-info animate-inout" :style="{height:openFlag[pid]?(list?.length||0)*40+50+'px':0}">
            <ul>
              <li class="detail-header flex jc-between ai-center">
                <span class="s1">{{ language.text_3_2 }}</span>
                <span class="s2">{{ language.type }}</span>
                <span class="s3">{{ language.amount }}</span>
                <span class="s4 ta-center">{{ language.label_3_4 }}</span>
              </li>
              <li class="detail-row flex jc-between ai-center text" v-for="rec in list" :key="rec.id">
                <span class="s1">{{ dateFormatter(rec.createdAt).format('yyyy-MM-dd') }}</span>
                <span class="s2" :class="{green:rec.operationType===2,red:rec.operationType>2}">{{
                    operationFormatter(rec.operationType)
                  }}</span>
                <span v-if="rec.operationType===1" class="s3">{{ amountFormatter(rec.rewardAmount) }}</span>
                <span v-else class="s3" :class="rec.operationType===2?'green':'red'">{{
                    (rec.operationType === 2 ? '+' : '-') + amountFormatter(rec.amount)
                  }}</span>
                <span class="s4 ta-center">
                <img class="icon-m" src="../../images/option/block.png" @click="blockLink(rec.hash)" alt=""/>
              </span>
              </li>
            </ul>
          </div>
        </li>
      </template>
      <li class="no-data flex jc-center ai-center" v-show="!loading&&recordStore.length===0">
        <p class="text fs-16 ta-center">
          <img src="../../images/no-data.png" alt="nodata"><br>{{ language.no_data }}
        </p>
      </li>
    </ul>
    <div v-if="loading" class="mask flex jc-center ai-center ta-center">
      <span><i class="icon-loading"></i><br>{{ language.tip_loading }}...</span>
    </div>
  </div>
</template>

<script>

import {mapActions, mapGetters, mapState} from "vuex";
import {dateFormatter} from "@/common/dataConvertor";
import {blockConfig} from "@/common/walletConfig";

export default {
  name: "farmRecord",
  computed: {
    ...mapState('app', ['address',]),
    ...mapState('farm', ['farmStore', 'recordStore']),
    ...mapGetters(['language']),
  },
  data() {
    return {
      loading: false, //
      openFlag: {},
    }
  },
  methods: {
    ...mapActions('farm', ['setFarmRecord']),
    dateFormatter,
    operationFormatter(type) {
      let map = {1: 'btn_reap', 2: 'btn_put_in', 3: 'btn_take_out'};
      return this.language[map[type]];
    },
    amountFormatter(amount) {
      return Number(amount).toFixed(2)
    },
    toggleOpened(pid) {
      let {openFlag} = this;
      openFlag[pid] = !openFlag[pid];
      // openFlag[pid]&&this.getList(pid);
    },
    getList(pid) {
      let {address} = this;
      this.loading = true;
      this.$api.gqlFarmRecord(address, pid).then(({operationRecords}) => {
        this.setFarmRecord(operationRecords);
        this.loading = false;
      }).catch(() => this.loading = false)
    },
    blockLink(hash) {
      window.open(blockConfig.host + hash);
    }
  },
  beforeMount() {
    this.getList();
  }
}
</script>

<style lang="less" scoped>
.farm-record {
  height: 100%;
  position: relative;
}
.m-folder-list {
  height: 100%;
  overflow: auto;
  cursor: default;
}
.m-folder-item {
  color: #b7b7cf;
  &:not(:first-child) {
    margin-top: 3px;
  }
}
.no-data {
  height: 100%;
  line-height: 1.8;
}
.main-info {
  position: relative;
  padding: 0 65px 0 25px;
  height: 80px;
  line-height: 35px;
  background-color: #353b4d;
  .c1 {
    width: 110px;
  }
  .c2 {
    width: 90px;
  }
  .c3 {
    width: 120px;
    text-align: right;
  }
  &:hover {
    background-color: #464d65;
  }
  .arrow {
    position: absolute;
    top: 50%;
    right: 15px;
    transform: translateY(-50%);
  }
}
.detail-info {
  overflow: hidden;
  padding: 0 25px;
  ul {
    padding: 10px 0;
  }
  .detail-header {
    height: 30px;
  }
  .detail-row {
    height: 40px;
  }
  .s1 {
    width: 85px;
  }
  .s2 {
    width: 70px;
  }
  .s3 {
    width: 20%;
  }
  .s4 {
    width: 70px;
    .icon-m {
      display: block;
      margin: 0 auto;
      cursor: pointer;
    }
  }
}
.base-info {
  padding: 15px 20px 0;
  line-height: 35px;
  .bet-num {
    font-size: 25px;
  }
}
@media screen and (max-width: 540px) {
  .m-folder-list {
    font-size: 12px;
  }
  .main-info {
    padding: 0 45px 0 15px;
    .c1{
      width: 100px;
    }
    .c2{
      width: auto;
    }
    .c3{
      width: 90px;
    }
    .arrow {
      right: 5px;
    }
  }
  .detail-info {
    padding: 0 15px;
  }
  .base-info {
    padding: 10px 15px 0;
    line-height: 30px;
  }
  .opened .detail-info {
    height: 231px;
  }
  .fs-20 {
    font-size: 16px;
  }
}
::-webkit-scrollbar {
  display: none;
}
</style>