<template>
  <button class="m-button" :class="{loading:m_loading,disabled}" :disabled="disabled"
       @click="clickHandle()" :style="style">
    <span class="inner flex jc-center ai-center">
      <slot></slot>
    </span>
    <i v-show="m_loading" class="icon-loading" :style="loadingStyle"></i>
  </button>
</template>

<script>

export default {
  name: 'mButton',
  props: {
    loading:{type:Boolean},
    height: {type: Number, default: 40},
    borderWidth:{type: Number,default: 2},
    padding: {type: Number, default: 15},
    disabled: {type: Boolean, default: false},
    onClick:{type:Function,default:function (){}}
  },
  data() {
    return {
      m_loading:false,
    };
  },
  watch:{
    loading(bool){
      this.m_loading = bool;
    }
  },
  computed: {
    style(){
      let {height,padding,borderWidth}=this;
      return {
        height:height + 'px',
        padding:`0 ${padding}px`,
        borderWidth: borderWidth+'px'
      }
    },
    loadingStyle(){
      let {height,borderWidth}=this;
      height = height - borderWidth*2 - 4;
      return {
        width: `${height}px`,
        height: `${height}px`,
        marginTop:`-${height*0.5}px`,
        marginLeft:`-${height*0.5}px`,
      }
    },
  },
  methods:{
    clickHandle(){
      if (this.loading) return;
      this.onClick();
    },
    setLoading(bool=true){
      this.m_loading = bool;
    }
  }
};
</script>

