import {BN} from "@/common/util";

export default {
  namespaced: true,
  state: {
    symbol: 'BNBUSDT',
    contractName: 'EthPricePrediction',
    resolution: '5',
    currentBlock: '',
    currentRounds: {},
    currentPrice: {},
    shortcutEpoch:sessionStorage.getItem('shortcut_epoch')||'',
  },
  mutations: {
    setSymbol(state, symbol) {
      state.symbol = symbol;
    },
    setContractName(state, contractName) {
      state.contractName = contractName;
    },
    setCurrentBlock(state, block) {
      state.currentBlock = block;
    },
    setCurrentPrice(state, {symbol, price}) {
      state.currentPrice[symbol] = new BN(price).toDisplay();
    },
    setResolution(state, resolution) {
      state.resolution = resolution;
    },
    setShortcutEpoch(state, shortcutEpoch) {
      state.shortcutEpoch = shortcutEpoch;
      sessionStorage.setItem('shortcut_epoch',shortcutEpoch);
    },
    setCurrentRound(state, {resolution, roundInfo}) {
      let round = state.currentRounds[resolution]||{};
      state.currentRounds[resolution] = Object.assign(round, roundInfo);
    },
  },
  actions: {
    setSymbol({commit}, pair) {
      commit('setSymbol', pair);
    },
    setCurrentBlock({commit}, block) {
      commit('setCurrentBlock', block);
    },
    setCurrentPrice({commit}, price) {
      commit('setCurrentPrice', price);
    },
    setResolution({commit}, resolution) {
      commit('setResolution', resolution);
    },
    setShortcutEpoch({commit}, shortcutEpoch) {
      commit('setShortcutEpoch', shortcutEpoch);
    },
    setCurrentRound({commit}, data) {
      commit('setCurrentRound', data);
    },
    setContractName({commit}, data) {
      commit('setContractName', data);
    },
  }
}