/**
 * graph
 * @param {String} tokensQuery
 * @returns {Promise<unknown>}
 */
export function optionGraphql(tokensQuery) {
  return new Promise((resolve, reject) => {
    optionClient.request(tokensQuery).then(data => {
      console.debug("option data: ", data);
      resolve(data);
    }).catch(err => {
      console.debug("Error option data: ", err);
      reject(err);
    });
  })
}

