import {createRouter, createWebHashHistory} from 'vue-router'
import store from '@/store'

let {commit} = store;
const routes = [
  {
    path: '/:pathMatch(.*)*',
    redirect: '/index',
  },
  {
    path: '/index',
    name: 'index',
    meta:{
      title: 'menu_1',
    },
    component: () => import('@/views/home')
  },
  {
    path: '/social',
    name: 'social',
    meta:{
      title: 'menu_1',
    },
    component: () => import('@/views/social')
  },
  {
    path: '/option',
    name: 'option',
    meta:{
      title: 'menu_2',
    },
    component: () => import('@/views/options')
  },
  {
    path: '/farm',
    name: 'farm',
    meta:{
      title: 'menu_3',
    },
    component: () => import('@/views/farm')
  },
  {
    path: '/nft',
    name: 'nft',
    meta:{
      title: 'nft',
    },
    redirect: '/nft/lottery',
    component: () => import('@/views/nftMain'),
    children: [
      {
        path: 'lottery',
        name: 'lottery',
        component: () => import('@/views/nft/nftLottery')
      },
      {
        path: 'bag',
        name: 'bag',
        component: () => import('@/views/nft/nftBag')
      },
    ]
  },
  {
    path: '/vip',
    name: 'vip',
    meta:{
      title: 'menu_5'
    },
    redirect: '/vip/member',
    component: () => import('@/views/vipMain'),
    children: [
      {
        path: 'member',
        name: 'member',
        component: () => import('@/views/vip/member')
      },
      {
        path: 'invitation',
        name: 'invitation',
        component: () => import('@/views/vip/invitation')
      },
    ]
  },
  {
    path: '/statistics',
    name: 'statistics',
    meta:{
      title: 'menu_7',
    },
    component: () => import('@/views/statistics')
  },
  {
    path: '/manager',
    name: 'manager',
    meta:{
      title: 'menu_9',
    },
    component: () => import('@/views/manager')
  },
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

router.beforeEach((to) => {
  (to.matched[0]&&to.matched[0].meta.title)&&commit('app/setTitle',to.matched[0].meta.title);
  // store.state.app.device==='mobile'&&commit('app/setMenuHidden',false);
  if (to.name === 'option') {
    let {symbol, resolution} = to.query;
    symbol && commit('option/setSymbol', symbol);
    resolution && commit('option/setResolution', resolution);
  }
});

export default router
