    <footer class="flex ai-center jc-between">
      <div class="bar-list flex ai-center">
        <a href="https://t.me/defx_global" target="_blank" title="telegram">
          <img class="contact-icon icon-s d-b" src="./images/telegram@2x.png" alt="">
        </a>
        <a href="https://twitter.com/DEFX_EN" target="_blank" title="twitter">
          <img class="contact-icon icon-s d-b" src="./images/twitter@2x.png" alt="">
        </a>
        <a href="https://fb.me/DEFX.Finance" target="_blank" title="facebook">
          <img class="contact-icon icon-s d-b" src="./images/facebook@2x.png" alt="">
        </a>
<!--        <a href="javascript:void(0);" title="Instagram">-->
<!--          <img class="contact-icon d-b" src="./images/Instagram@2x.png" alt="">-->
<!--        </a>-->
<!--        <a href="javascript:void(0);" title="whatsapp">-->
<!--          <img class="contact-icon d-b" src="./images/whatsapp-fill@2x.png" alt="">-->
<!--        </a>-->
<!--        <a href="javascript:void(0);" title="Discord">-->
<!--          <img class="contact-icon d-b" src="./images/discord@2x.png" alt="">-->
<!--        </a>-->
<!--        <a href="javascript:void(0);" title="Reddit">-->
<!--          <img class="contact-icon d-b" src="./images/reddit@2x.png" alt="">-->
<!--        </a>-->
      </div>
      <span class="copyright">{{ language.copyright }}</span>
    </footer>
  </div>
</template>
<script>
import HeadBar from "@/views/common/headBar";
import LeftMenu from "@/views/common/leftMenu";
import {mapActions, mapGetters, mapState} from "vuex";
import {checkDevice} from "@/common/util";

export default {
  components: {LeftMenu, HeadBar},
  computed:{
    ...mapState('app',['menuHidden','locale']),
    ...mapGetters(['language']),
  },
  methods:{
    ...mapActions('app',['setMenuHidden','setDevice']),
    checkApp(){
      this.setDevice(checkDevice());
    }
  },
  beforeMount() {
    window.addEventListener('resize',()=>{
      this.checkApp();
    })
  }
}
</script>
<style lang="less" scoped>
.page-layout {
  height: 100%;
  padding: 60px 0 0;
}
.page-main{
  height: calc(100% - 50px);
  overflow: auto;
  margin-left: 225px;
}
footer{
  margin-left: 225px;
  height: 50px;
  background-color: #191f2d;
  padding: 0 15px;
}
.hidden{
  .page-main,footer{
    margin-left: 65px;
  }
}
.bar-list{
  a{
    &:not(:first-child){
      margin-left: 10px;
    }
  }
}
.mask{
  z-index: 9;
  display: none;
}
@media screen and (max-width: 1024px) {
  .page-main{
    padding: 0;
    margin-left: 0!important;
  }
  footer{
    margin-left: 0!important;
    height: 50px;
    background-color: #191f2d;
    padding: 0 15px;
  }
  .mask{
    display: block;
  }
}
@media screen and (max-width: 768px) {
  .page-main{
    &.lottery,&.member,&.farm,&.invitation{
      height: auto;
      min-height: calc(100% - 50px);
    }
  }
}
</style>