<template>
  <ul class="m-tab flex">
    <li class="m-tab-item" :class="{active:index===tabIndex}"
        v-for="(tab,index) in tabs"
        :style="`margin-right: ${gap}px;`"
        @click="onClick(index,tab)"
        :key="index">
      <span>{{ tab.language_key?language[tab.language_key]:tab.label }}</span>
    </li>
  </ul>
</template>

<script>
import {mapGetters} from "vuex";

export default {
  name: "mTabs",
  emits:['update:tabIndex'],
  props:{
    enable:{type:Boolean,default:true},
    tabIndex:{type:Number,require:true},
    tabs:{
      type:Array,default:()=>[
        {type:0,label:''},
        {type:1,label:''},
        {type:2,label:''},
      ]
    },
    gap:{type:Number, default:20},
    onTabChange:{type: Function,default:function (){}}
  },
  data(){
    return {
    }
  },
  computed:{
    ...mapGetters(['language']),
  },
  methods:{
    onClick(index,tab){
      if (!this.enable || index===this.tabIndex) return;
      this.$emit('update:tabIndex', index);
      this.onTabChange(index,tab);
    }
  },
  mounted() {

  }
}
</script>

<style lang="less" scoped>
.m-tab{
  line-height: 50px;
}
.m-tab-item{
  height: 100%;
  position: relative;
  padding: 0 5px;
  cursor: pointer;
  &:last-child{
    margin-right: 0!important;
  }
  &.active{
    color: white;
    &:after{
      display: block;
      content: '';
      height: 2px;
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: #FFFFFF;
      box-shadow: 0 0 .4rem .1rem #00e1ff;
    }
  }
}
</style>