<template>
  <div class="round-bar flex">
    <coin-selector/>
    <ul class="resolutions flex">
      <li v-for="res in resolutions" class="bar animate"
          :class="{'shadow-active': resolution===res.type}"
          :key="res.type" @click="res.online&&changeResolution(res.type)">
        <resolution-counter class="res-counter" :item="res"/>
      </li>
    </ul>
  </div>
</template>

<script>
import {mapActions, mapState} from "vuex";
import ResolutionCounter from "@/views/optionComps/resolutionCounter";
import CoinSelector from "@/views/common/coinSelector";

export default {
  name: "roundBar",
  components: {CoinSelector, ResolutionCounter},
  data() {
    return {
      resolutions: [
        {text: 'resolution_5', type: '5', online: true},
        {text: 'resolution_15', type: '15', online: true},
      ],
      blockTimer: null,
    }
  },
  computed: {
    ...mapState('option',['symbol', 'resolution']),
  },
  methods: {
    ...mapActions('option',['setCurrentBlock']),
    getCurrentBlock() {
      clearInterval(this.blockTimer);
      this.blockTimer = setInterval(() => {
        this.$api.getBlock('pending').then(({number}) => {
          this.setCurrentBlock(number);
        })
      }, 1000);
    },
    changeResolution(resolution) {
      let {symbol} = this;
      this.$router.push({path: '/option', query: {symbol, resolution}})
    }
  },
  beforeMount() {
    this.getCurrentBlock();
  },
}
</script>

<style lang="less" scoped>
.round-bar {
  position: relative;
  background-color: #191F2D;
}
.resolutions{
  margin-left: 15px;
}
.bar {
  width: 100px;
  &:not(:first-child) {
    margin-left: 10px;
  }
  &.shadow-active {
    background-color: #353B4D;
  }
}
@media screen and (max-width: 1024px) {
  .round-bar {
    width: 100%;
    position: fixed;
    left: 0;
    top: 65px;
  }
}
@media screen and (max-width: 540px) {
  .bar {
    width: 33.3333%;
  }
  .resolutions {
    margin-top: 10px;
    margin-left: 0;
    width: 100%;
    padding: 0 5px;
    .res-counter{
      height: 40px;
    }
  }
  .round-bar {
    flex-flow: wrap;
  }
}
</style>