export default {
  namespaced: true,
  state: {
    market: {},
    myCards: [], //
  },
  mutations: {
    setMarket(state, market) {
      state.market = market;
    },
    setMyCards(state, arr) {
      state.myCards = arr;
    },
    deleteCard(state, nftIds) {
      if (nftIds instanceof Array) {
        nftIds.forEach(id => {
          let idx = state.myCards.findIndex(card => card.id === id);
          idx !== -1 && state.myCards.splice(idx, 1);
        })
      }
    },
  },
  actions: {
    setMarket({commit}, market) {
      commit('setMarket', market);
    },
    setMyCards({commit}, arr) {
      commit('setMyCards', arr);
    },
    deleteCard({commit}, nftIds) {
      commit('deleteCard', nftIds);
    },
  },
}