<template>
  <div class="left-menu flex flex-column" :class="{hidden: menuHidden}">
    <router-link class="logo flex ai-center" to="/index">
      <img src="../../images/option/logo.png" alt="LOGO"/>
    </router-link>
    <ul class="flex1">
      <li class="animate-inout" v-for="m in list" :key="m.text" :class="{active:checkLink(m.path)}">
        <router-link v-if="m.path" class="menu-item menu-link flex ai-center text" :to="m.path" @click="clickHandle">
          <img class="item-icon icon" :src="m.icon" :alt="language[m.text]">
          <span class="item-text fs-16">{{ language[m.text] || m.text}}</span>
        </router-link>
        <div v-else-if="m.children" :class="{folded:m.open}">
          <div class="menu-item flex ai-center text relative" @click="togSubmenu(m)">
            <img class="item-icon icon" :src="m.icon" :alt="language[m.text]">
            <span class="item-text fs-16">{{ language[m.text] || m.text}}</span>
            <img class="item-arrow sub-arrow" src="../../images/option/order1.png" alt="">
          </div>
          <ul class="sub-menu text">
            <li v-for="sm in m.children" class="animate-inout" :class="{active:checkLink(sm.path)}" :key="sm.text">
              <router-link v-if="sm.path" class="menu-item menu-link flex ai-center text" :to="sm.path" @click="clickHandle">
                <span class="fs-16">{{ language[sm.text] || sm.text}}</span>
              </router-link>
              <a v-else-if="sm.href" class="menu-item menu-link flex ai-center text" :href="sm.href" target="_blank">
                <span class="fs-16">{{ language[sm.text] || sm.text}}</span>
              </a>
              <a v-else-if="sm.handle" @click.prevent="menuHandler(sm.handle, sm)" class="menu-item menu-link flex ai-center text" href="/" >
                <span class="fs-16">{{ language[sm.text] || sm.text}}</span>
              </a>
            </li>
          </ul>
        </div>
      </li>
    </ul>
    <div class="bottom-bar">
      <div class="setting-row">
        <div v-show="menuHidden" class="app-setting flex jc-center ai-center">
          <img class="icon" src="../../images/set.png" @click="setMenuHidden(false)" alt="setting">
        </div>
        <div class="flex jc-center">
          <locale-bar isStore/>
        </div>
      </div>
    </div>
    <!-- <div class="global-video-box">
      <div class="global-video-mask"></div>
      <video width="80vw" class="global-video-video">
          <source src="../../video/project1.mp4" type="video/mp4" />
      </video>
    </div> -->
  </div>
</template>

<script>
import {mapActions, mapGetters, mapState} from "vuex";
import icon_1 from '@/images/home.png'
import icon_2 from '@/images/Bet.png'
import icon_3 from '@/images/farm.png'
import icon_4 from '@/images/copy.png'
import icon_5 from '@/images/member.png'
import icon_6 from '@/images/more.png'
// import project1 from '@/video/project1.mp4'
// import project2 from '@/video/project2.mp4'
// import project3 from '@/video/project3.mp4'
// import icon_9 from '@/images/partner.png'
import LocaleBar from "@/views/common/localeBar";
import {balanceFormatter} from "@/common/dataConvertor";
import {coinMap} from "@/common/walletConfig";
import creatVideo from './creatVideo'

export default {
  name: "leftMenu",
  components: {LocaleBar},
  data() {
    return {
      coinMap,
      list: [
        {icon: icon_1, text: 'menu_1', path: '/index'},
        {icon: icon_2, text: 'menu_2', path: '/option'},
        {icon: icon_4, text: 'nft', path: '/nft'},
        {icon: icon_3, text: 'menu_3', path: '/farm'},
        {icon: icon_5, text: 'menu_5', path: '/vip'},
        {icon: icon_6, text: 'menu_6', open:true,
          children:[
            {text: 'menu_9', path: '/manager'},
            {text: 'menu_11',href: 'https://www.rdauditors.com/wp-content/uploads/2021/10/DEFX-Smart-Contracts-Security-Report.pdf'},
            {text: 'menu_10',path: '/social'},
            // {text: 'menu_7',path: '/statistics'},
            {text: 'menu_8',href: './lib/defx-white-paper.pdf'},
            {text: 'Project Introduction',href: './lib/defx-en.pdf'},
            // {text: 'menu_12',handle: 'handleOpenVideo', videoSrc: project1},
            // {text: 'menu_13',handle: 'handleOpenVideo', videoSrc: project2},
            // {text: 'menu_14',handle: 'handleOpenVideo', videoSrc: project3},
            {text: 'GitHub',href: 'https://github.com/defx-vip/price-pridiction'},
          ]
        },
      ],
      balanceList: ['BNB', 'DFT']
    }
  },
  computed: {
    ...mapState('app', ['device', 'menuHidden', 'address', 'balance']),
    ...mapGetters(['language']),
  },
  methods: {
    balanceFormatter,
    ...mapActions('app', ['setMenuHidden']),
    checkLink(path) {
      return this.$route.path.indexOf(path) !== -1;
    },
    menuHandler(method, params) {
      if (typeof this[method] === 'function') {
        this[method](params)
      }
    },
    handleOpenVideo(params) {
      console.log('handleOpenVideo', params)
      const dom = creatVideo(params.videoSrc)
      document.body.appendChild(dom)
    },
    clickHandle() {
      this.device === 'mobile' && this.setMenuHidden(true);
    },
    togSubmenu(menu){
      menu.open = !menu.open;
    }
  },
}
</script>

<style lang="less">
.global-video{
  &-box{
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 9998;
  }
  &-mask {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 10;
  }
  &-video{
    position: absolute;
    z-index: 20;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100vw;
    height: auto;
    // max-width: 80vw;
    // max-height: 80vh;
  }
}
</style>
<style lang="less" scoped>
.left-menu {
  width: 225px;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 2;
  background-color: #191f2d;
  overflow: auto;
  &.hidden{
    width: 65px;
  }
}
.logo {
  height: 60px;
  padding-left: 5px;
  background-color: #191F2D;
  img {
    height: 50px;
    display: block;
  }
}
li {
  width: 100%;
  position: relative;
  &.active {
    .menu-item{
      background-color: #353B4D!important;
    }
  }
}
.menu-item {
  height: 50px;
  padding: 0 30px 0 15px;
  white-space: nowrap;
  cursor: pointer;
}
.item-text {
  margin-left: 15px;
}
.item-arrow {
  width: 30px;
  position: absolute;
  top: 50%;
  right: 0;
  &.sub-arrow{
    transform: translateY(-50%) rotate(0deg);
  }
}
.bottom-bar {
  padding: 0 10px;
}
.setting-row {
  height: 50px;
}
.app-setting {
  width: 45px;
  height: 50px;
  cursor: pointer;
  .icon {
    cursor: pointer;
  }
}
.hidden{
  .item-text{
    display: none;
  }
  .sub-arrow{
    opacity: 0;
  }
}
.folded{
  .sub-arrow{
    transform: translateY(-50%) rotate(180deg);
  }
  .sub-menu{
    display: none;
  }
}
@media screen and (min-width: 1025px) {
  .menu-link{
    &:hover{
      background-color: #222739;
    }
  }
  .hidden{
    li{
      .sub-menu{
        display: none;
        //display: block;
        position: absolute;
        top: -5px;
        left: 100%;
        transform: translate(15px);
        background-color: #191f2d;
        padding: 5px;
        //box-shadow: 0 0 5px 2px rgba(0,0,0,.4);
        &:before{
          content: "";
          position: absolute;
          width: 15px;
          top: 0;
          right: 100%;
          bottom: 0;
        }
        &:after{
          content: "";
          position: absolute;
          border: 10px solid transparent;
          border-right: 10px solid #191f2d;
          top: 20px;
          right: 100%;
        }
      }
    }
    li:hover{
      .sub-menu{
        display: block;
      }
    }
  }
}
@media screen and (max-width: 1024px) {
  .left-menu {
    z-index: 10;
    transition: all ease-in-out .2s;
    &.hidden{
      left: -100%;
    }
  }
}
</style>