import BigNumber from 'bignumber.js';
import {BConfig, blockConfig} from "@/common/walletConfig";
import zh_CN from '@/common/18n/zh_CN';
import zh_TW from '@/common/18n/zh_TW';
import en from '@/common/18n/en';
import kr from '@/common/18n/kr';
import ja from '@/common/18n/ja';
import es from '@/common/18n/es';
import ar_AE from '@/common/18n/ar_AE';
import fr from '@/common/18n/fr';
import de_DE from '@/common/18n/de_DE';

const locales = {
  en,kr,ja,ar_AE,fr,de_DE,es,zh_CN,zh_TW
}
/**
 * Date Date String
 * (y) 1-4 (S) 1 ( 1-3 )
 * 
 * (new Date()).format("yyyy-MM-dd hh:mm:ss.S") ==> 2006-07-02 08:09:04.423
 * (new Date()).format("yyyy-M-d h:m:s.S")      ==> 2006-7-2 8:9:4.18
 * @param {String} fmt 
 * @returns {*}
 */
Date.prototype.format = function (fmt) { //author: meizz
  var o = {
    "M+": this.getMonth() + 1, //
    "d+": this.getDate(), //
    "h+": this.getHours(), //
    "m+": this.getMinutes(), //
    "s+": this.getSeconds(), //
    "q+": Math.floor((this.getMonth() + 3) / 3), //
    "S": this.getMilliseconds() //
  };
  if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
  for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
  return fmt;
}
export const maxUint256 = new BigNumber('0xffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff');
BigNumber.prototype.toWei = function (b_type = 'bnb') {
  let {decimal} = BConfig[b_type];
  return this.times(Math.pow(10, decimal));
};
BigNumber.prototype.toDisplay = function (b_type = 'bnb') {
  let {decimal} = BConfig[b_type];
  return this.div(Math.pow(10, decimal));
};
BigNumber.prototype.myFixed = function (b_type = 'bnb') {
  let {fixedDecimal} = BConfig[b_type];
  return this.isZero() ? '0' : this.toFixed(fixedDecimal);
};
BigNumber.prototype.toRate = function (n = 2) {
  return this.isZero() ? 0 : this.toFixed(n);
};

export const BN = BigNumber;

export const counter = {
  /**
   * 
   * @param startBlock {BN|Number|String} 
   * @param endBlock {BN|Number|String} 
   * @param isReal {Boolean} 
   * @returns {number}
   */
  calClockCount(startBlock, endBlock, isReal = false) {
    let deltaTime = blockConfig.deltaTime;
    let s = new BN(endBlock).minus(new BN(startBlock));
    return !isReal && s.lt(0) ? 0 : s.times(deltaTime).toNumber();
  },
  /**
   * 
   * @param second {int|String} 
   * @returns {string}
   */
  format(second) {
    second = second > 0 ? second : 0;
    // d,h,m,s
    let h, m, s;
    h = Math.floor(second / 60 / 60 % 24);
    m = Math.floor(second / 60 % 60);
    s = Math.floor(second % 60);
    return `${h < 10 ? '0' + h : h}:${m < 10 ? '0' + m : m}:${s < 10 ? '0' + s : s}`;
  }
}

export function getLanguage(locale) {
  return locales[locale];
}

export function checkDevice() {
  let w = window.outerWidth;
  return w<=1024?'mobile':'pc'
}