<template>
<div class="coin-selector">
  <div class="pair flex ai-center"
      @click="listShow=!listShow">
    <img class="b-icon" :src="currentPairInfo.icon" alt="">
    <div class="display">
      <p class="symbol flex ai-center">
        <span class="d-ib" style="min-width: 75px;">{{ currentPairInfo.display }}</span>
        <img class="arrow animate-inout" :class="{show:listShow}" src="../../images/option/arrow_down@3x.png" alt=""/>
      </p>
      <b class="price text">{{ currentPrice[symbol]?.myFixed()||'' }}</b>
    </div>
  </div>




  <!--    -->
  <div v-if="listShow" class="mask fixed" @click="listShow = false"></div>





  <ul class="pair-list animate-inout shadow-active" :class="{show:listShow}" @click="listShow = false">
    <li class="logo"><img src="../../images/option/logo.png" alt="LOGO"/></li>
    <li class="pair animate-inout flex ai-center" v-for="pair in coinPairs"
        :class="{'active':pair.symbol===symbol}"
        @click="setSymbol(pair.symbol);setContractName(pair.contractName)"
        :key="pair.symbol">
      <img class="b-icon" :src="pair.icon" alt="">
      <span class="display">
          {{ pair.display }}<br>
          <b class="price text">{{ currentPrice[pair.symbol]?.myFixed()||'' }}</b>
        </span>
    </li>
  </ul>
</div>
</template>

<script>
import {mapActions, mapState} from "vuex";
import icon_bnb from "@/images/option/BNB.png";
import icon_eth from "@/images/coins/ETH.svg";

export default {
  name: "coinSelector",
  data(){
    return {
      coinPairs: [
        {symbol:'BNBUSDT',display: 'BNB/USDT', contractName: 'BnbPricePrediction', priceContractName: 'AggregatorV3InterfaceBnb', icon: icon_bnb},
        {symbol:'ETHUSDT',display: 'ETH/USDT', contractName: 'EthPricePrediction', priceContractName: 'AggregatorV3InterfaceEth', icon: icon_eth},
      ],
      timer:null,
      listShow: false,
    }
  },
  computed:{
    ...mapState('option',['symbol','currentPrice']),
    currentPairInfo() {
      return this.coinPairs.find(p=>p.symbol===this.symbol);
    }
  },
  methods:{
    ...mapActions('option',['setSymbol','setCurrentPrice', 'setContractName']),
    getPriceOfCoinPairs(){
      this.timer = setInterval(()=>{
        this.coinPairs.map(({priceContractName, symbol})=>{
          this.$api.getLatestPrice(priceContractName).then(res=>{
            this.setCurrentPrice({symbol,price:res.answer});
          })
        });
      },1000);
    }
  },
  beforeMount() {
    console.debug(this)
    this.getPriceOfCoinPairs();
    let pair = this.coinPairs[0];
    this.setSymbol(pair.symbol);
    this.setContractName(pair.contractName)
  },
  unmounted() {
    clearInterval(this.timer);
  }
}
</script>

<style lang="less" scoped>

.pair {
  height: 60px;
  padding-left: 15px;
  line-height: 24px;
  cursor: pointer;
  position: relative;
  .b-icon {
    height: 38px;
    margin-right: 15px;
  }
  .display {
    font-size: 13px;
  }
  .price {
    font-size: 16px;
  }
  .arrow {
    width: 30px;
    &.show {
      transform: rotate(180deg);
    }
  }
}
.pair-list {
  width: 180px;
  position: absolute;
  top: 70px;
  left: -2px;
  overflow: hidden;
  border-radius: 10px;
  border: 2px solid #FFFFFF;
  z-index: -1;
  background-color: #191f2d;
  transform: translateY(-100%-30px);
  &.show {
    transform: translateY(0);
  }
  .logo{
    display: none;
  }
  .pair {
    &:hover {
      background-color: #242e40;
    }
    &.active {
      background-color: #323b55;
    }
  }
}
.mask{
  z-index: 9;
  display: none;
}
@media screen and (max-width: 1024px) {
  .mask{
    display: block;
  }
  .pair-list{
    width: 50%;
    max-width: 260px;
    height: 100%;
    transform: translateX(-100%);
    position: fixed;
    top: 0;
    left: 0;
    border: none;
    border-radius: 0;
    z-index: 9;
    box-shadow: none;
    .logo{
      display: block;
      img{
        width: 100%;
      }
    }
    &.show {
      transform: translateX(0);
    }
  }
}
@media screen and (max-width: 540px) {
  .pair{
    height: 50px;
  }
}
</style>