import {BN} from "@/common/util";
import {nftQuality} from "@/common/walletConfig";

export function optionRoundConvertor(round) {
  let {bearAmount, bullAmount, totalAmount, lockPrice, closePrice} = round;
  totalAmount = new BN(totalAmount).toDisplay('balance');
  bullAmount = new BN(bullAmount).toDisplay('balance');
  bearAmount = new BN(bearAmount).toDisplay('balance');
  lockPrice = new BN(lockPrice || 0).toDisplay();
  closePrice = new BN(closePrice || 0).toDisplay();
  let bullRate = bullAmount.gt(0) ? totalAmount.div(bullAmount) : new BN(0);
  let bearRate = bearAmount.gt(0) ? totalAmount.div(bearAmount) : new BN(0);
  return {
    ...round,
    bearAmount, bullAmount, totalAmount, lockPrice, closePrice,
    bullRate, bearRate
  }
}

export function inputNumberFixer(val) {
  let idx = val.indexOf('.');
  return idx === -1 ? val : val.substring(0, idx + 5);
}

export function addressFormat(address) {
  return address ? address.substr(0, 4) + '....' + address.substr(-4, 4) : '';
}

/**
 * 
 * @param {BN} balance
 * @param {Number} n
 * @returns {*}
 */
export function balanceFormatter(balance, n) {
  let b = balance.toDisplay('balance');
  return typeof n === "number" ? b.toFixed(n) : b.myFixed('balance');
}

/**
 * 
 * @param {BN | Number | String} amount
 * @param {Number} n
 * @returns {*}
 */
export function amountFormatter(amount, n = 2) {
  if (!(amount instanceof BN)) amount = new BN(amount || 0);
  return amount.isZero() ? 0 : amount.toFixed(n)
}

export function bagNftConvertor(row) {
  let {grade, amount, createdTime} = row;
  amount = new BN(amount).toDisplay('balance').myFixed('balance');
  createdTime = dateFormatter(createdTime).format('yyyy-MM-dd');
  let cfg = nftQuality[grade];
  return {
    ...row,
    color: cfg.color,
    amount,
    createdTime
  }
}

export function poolInfoConvertor(row) {
  let {allocPoint, token0symbol, token1symbol} = row;
  allocPoint = new BN(allocPoint).div(1000).toString();
  let farmPair = token0symbol + ' / ' + token1symbol;
  return {
    ...row,
    allocPoint,
    farmPair
  }
}

export function dateFormatter(str) {
  return new Date(Number(str + '000'))
}

export function addressRex(address) {
  return /^0x[a-fA-F0-9]{40}$/.test(address);
}