<template>
  <div class="locale-bar flex ai-center" :class="{show}" @click.stop="show=!show">
    <img class="icon-s animate-inout" src="../../images/lang@2x.png" alt="">
    <m-button class="btn hdr-el-blue" :height="30">
      {{ countries[showLocale].text }}
    </m-button>
    <ul class="shadow-active">
      <li v-for="(f,key) in countries" :class="{active:key===showLocale}" @click="changeHandle(key)" :key="key">
        {{ f.text }}
      </li>
    </ul>
  </div>
</template>

<script>
import {mapActions, mapState} from "vuex";
import MButton from "@/components/mButton";

export default {
  name: "localeBar",
  components: {MButton},
  computed: {
    ...mapState('app', ['locale']),
    countries() {
      const data = {
        'en': {text: 'English'},
        'kr': {text: ''},
        'ja': {text: ''},
        'es': {text: 'Espaol'},
        'fr': {text: 'French'},
        'de_DE': {text: 'Deutsch'},
        'ar_AE': {text: ''},
        'zh_CN': {text: ''},
        'zh_TW': {text: ''},
      }
      if (this.hasAll) {
        return {
        'all': {text: 'All'},
          ...data,
        }
      }
      return data
    },
    showLocale() {
      if (this.isStore) return this.locale
      return this.value || this.locale
    }
  },
  props: {
    isStore: Boolean,
    hasAll: Boolean,
    value: String,
  },
  data() {
    return {
      // countries: {
      //   'en': {text: 'English'},
      //   'kr': {text: ''},
      //   'ja': {text: ''},
      //   'es': {text: 'Espaol'},
      //   'fr': {text: 'French'},
      //   'de_DE': {text: 'Deutsch'},
      //   'ar_AE': {text: ''},
      //   'zh_CN': {text: ''},
      //   'zh_TW': {text: ''},
      // },
      show: false,
    }
  },
  methods: {
    ...mapActions('app', ['setLocale']),
    outerClick(){
      if (this.show) this.show = false;
    },
    changeHandle(key) {
      
      if (this.isStore) {
        this.setLocale(key)
      } else {
        this.$emit('change', key)
      }
    }
  },
  beforeMount() {
    window.addEventListener('click',this.outerClick);
  },
  unmounted() {
    window.removeEventListener('click',this.outerClick);
  }
}
</script>

<style lang="less" scoped>
.locale-bar {
  height: 50px;
  position: relative;
  .flag {
    height: 33px;
    display: block;
  }
  .icon-s {
    margin-right: 5px;
    transform: rotate(0);
  }
}
.btn {
  white-space: nowrap;
}
&.show {
  ul {
    display: block;
  }
  .icon-s {
    transform: rotate(180deg);
  }
}
ul {
  border-radius: 5px;
  position: absolute;
  bottom: calc(100% + 10px);
  right: 0;
  background-color: #222a3d;
  display: none;
}
li {
  padding: 0 15px;
  line-height: 30px;
  cursor: pointer;
  white-space: nowrap;
  &:hover {
    background-color: #353B4D;
  }
  &.active {
    color: #FFFFFF;
    background-color: #464d65;
  }
  &:first-child{
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
  }
  &:last-child{
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
  }
}
@media screen and (max-width: 768px) {
  .locale-bar {
    .flag {
      height: 25px;
    }
  }
}
</style>