<template>
  <div class="option-record">
    <div class="tab-bar flex ai-center">
      <m-tabs :tabs="tabs" v-model:tab-index="tabIndex"
              :enable="!loading"
              :on-tab-change="tabChangeHandle"/>
    </div>
    <div class="list-box">
      <ul class="m-folder-list">
        <li v-for="item in convertData" :key="item.id">
          <div class="main-info flex jc-between ai-center" @click="openFlag[item.id] = !openFlag[item.id]">
            <b class="win-text" :class="item.isWinner?'green':'red'">
              {{ item.isWinner ? language.win : language.lose }}
            </b>
            <span class="c1">{{ language.type }}<br><b class="text">{{ item.symbol }}</b></span>
            <span class="c2">
              {{ language.bet_direction }}<br>
              <b v-if="item.position==='Bull'" class="flex ai-center green">
                {{ roundMap[item.roundType] }}
                <img class="icon" src="../../images/option/up.png" alt=""/>
              </b>
              <b v-else class="flex ai-center red">
                {{ roundMap[item.roundType] }}
                <img class="icon" src="../../images/option/down.png" alt=""/>
              </b>
            </span>
            <span class="c3">{{ language.your_profit }}<br>
              <b class="text flex ai-center" v-if="item.isWinner">
                <img class="icon-s" src="../../images/option/jiangbei.png" alt=""/>
                +{{ item.gain }}
              </b>
              <b class="text flex ai-center" v-else>
                <img class="icon-s" src="../../images/option/nft-copy.png" alt=""/>
                {{ language.nft_card }}
              </b>
            </span>
            <img v-if="openFlag[item.id]" class="icon arrow" src="../../images/option/order1.png" alt=""/>
            <img v-else class="icon arrow" src="../../images/option/order.png" alt=""/>
          </div>
          <div class="detail-info animate-inout" :class="{open:openFlag[item.id]}">
            <p class="base-info flex jc-between">
              <span class="s1">
                {{ language.bet_num }}(USDT)<br>
                <b class="text fs-20">{{ item.amount }}</b>
              </span>
              <span class="flex1">{{ language.pool }}(USDT)<br>
                <span class="text">{{ item.round.totalAmount }}</span>
              </span>
              <span class="s3 ta-right text">{{ item.createdAt }}<br># {{ item.round.epoch }}</span>
            </p>
            <div class="res-box flex jc-between ai-center border-gray hdr-el-gray"
                 :class="{'border-green hdr-el-green':item.delta.gt(0),'border-red hdr-el-red':item.delta.lt(0)}"
            >
              <span class="s1">
                {{ language.end_price }}($)<br>
                <b class="fs-20 text" :class="{green:item.delta.gt(0),red:item.delta.lt(0)}">{{
                    item.round.closePrice
                  }}</b>
              </span>
              <span class="flex1">
                {{ language.delta_price }}($)<br>
                <span class="flex ai-center text" :class="{green:item.delta.gt(0),red:item.delta.lt(0)}">
                  {{ item.delta.myFixed() }}
                  <img v-if="item.delta.gt(0)" class="icon" src="../../images/option/up.png" alt=""/>
                  <img v-if="item.delta.lt(0)" class="icon" src="../../images/option/down.png" alt=""/>
                </span>
              </span>
              <span class="s3 ta-right">
                {{ language.lock_price }}($)<br>
                <span class="text">{{ item.round.lockPrice }}</span>
              </span>
            </div>
            <footer class="flex jc-between ai-center">
              <template v-if="item.isWinner">
                <img class="cap icon-xl" src="../../images/option/jiangbei.png" alt=""/>
                <p class="flex1">
                  <span class="res-text d-ib text">{{ language.win }}</span>{{ language.your_profit }}<br>
                  <b class="gain d-ib text fs-20">{{ item.gain }}</b>
                </p>
              </template>
              <template v-else>
                <img class="cap icon-xl" src="../../images/option/nft-copy.png" alt=""/>
                <p class="flex1">
                  <span class="res-text d-ib">{{ language.lose }}</span>{{ language.your_profit }} <br>
                  <b class="gain d-ib text fs-20">{{ item.gain }}</b>
                </p>
              </template>
              <p class="block flex jc-between ta-center" v-if="item.claimed">
                <span @click="openBlock(item.round.startHash)">
                  {{ language.start_block }}<br>
                  <img class="icon-l" src="../../images/option/block.png" alt=""/>
                </span>
                <span @click="openBlock(item.round.endHash)">
                  {{ language.end_block }}<br>
                  <img class="icon-l" src="../../images/option/block1.png" alt=""/>
                </span>
              </p>
              <m-button v-else class="btn-claim hdr-el-blue" :ref="'btn-'+item.id"
                        @click="claim(item.round.epoch,item.roundType, item.symbol, $refs['btn-'+item.id])"
              >
                {{ item.isWinner ? language.btn_claim_profit : language.btn_claim_nft }}
              </m-button>
            </footer>
          </div>
        </li>
        <li class="no-data flex jc-center ai-center" v-show="!loading&&convertData.length===0">
          <p class="text fs-16 ta-center">
            <img src="../../images/no-data.png" alt="nodata"><br>{{ language.no_data }}
          </p>
        </li>
      </ul>
      <div v-if="loading" class="mask flex jc-center ai-center ta-center">
        <span><i class="icon-loading"></i><br>{{ language.tip_loading }}...</span>
      </div>
    </div>
  </div>
</template>

<script>
import MTabs from "@/components/mTabs";
import {optionGraphql} from "@/common/api";
import {mapGetters, mapState} from 'vuex';
import {BN} from "@/common/util";
import {blockConfig, symbolContractMapping} from "@/common/walletConfig";
import MButton from "@/components/mButton";

export default {
  name: "optionRecord",
  components: {MButton, MTabs},
  computed: {
    ...mapGetters(['language']),
    ...mapState('app', ['address', 'device']),
    ...mapState('option', ['currentRounds', 'contractName']),
    convertData() {
      let arr = [];
      this.data.map((row) => {
        let {endBlock, failed} = row.round;
        (endBlock || failed) && arr.push(this.convertor(row));
      });
      return arr;
    },
  },
  watch: {
    currentRounds() {
      this.getList();
    },
  },
  data() {
    return {
      tabIndex: 0,
      tabs: [
        {type: 0, language_key: 'order_unclaimed'},
        {type: 1, language_key: 'order_claimed'},
      ],
      data: [],
      loading: false, //
      openFlag: {},
      roundMap: {
        '5': '5M',
        '15': '15M'
      },
    }
  },
  methods: {
    convertor(row) {
      let {round, amount, createdAt, position} = row;
      let {totalAmount, lockPrice, closePrice, rewardAmount, rewardBaseCalAmount} = round;
      amount = new BN(amount);
      totalAmount = new BN(totalAmount).myFixed();
      lockPrice = new BN(lockPrice || 0);
      closePrice = new BN(closePrice || 0);
      rewardAmount = new BN(rewardAmount || 0);
      rewardBaseCalAmount = new BN(rewardBaseCalAmount || 0);
      return {
        ...row,
        amount: amount.myFixed(),
        round: {
          ...round,
          totalAmount,
          lockPrice: lockPrice.eq(0) ? '????' : lockPrice.myFixed(),
          closePrice: closePrice.eq(0) ? '????' : closePrice.myFixed()
        },
        delta: closePrice.minus(lockPrice),
        isWinner: round.position === position,
        gain: round.position === position ? amount.times(rewardAmount).div(rewardBaseCalAmount).myFixed() : this.language.nft_card,
        createdAt: new Date(Number(createdAt + '000')).format('yyyy-MM-dd')
      }
    },
    getQuery() {
      let tq;
      switch (this.tabIndex) {
        case 0:
          tq = `claimed:false`;
          break;
        case 1:
          tq = 'claimed:true';
          break;
        default:
          tq = '';
      }
      return `query{
        bets(first:300,orderBy:block,orderDirection:desc,where:{user:"${this.address}",${tq}}) {
          id
          symbol
          round {
            id
            epoch
            totalAmount
            lockPrice
            closePrice
            startHash
            endHash
            position
            endBlock
            failed
            rewardAmount
            rewardBaseCalAmount
          }
          roundType
          amount
          position
          claimed
          nftTokenId
          createdAt
        }
      }`;
    },
    getList() {
      if (this.loading) return;
      this.loading = true;
      optionGraphql(this.getQuery()).then(({bets}) => {
        this.data = bets;
        this.loading = false;
      }).catch(() => {
        this.loading = false;
      })
    },
    tabChangeHandle() {
      this.getList();
    },
    claim(roundId, resolution, symbol, $el) {
      $el.setLoading(true);
      let contractName = symbolContractMapping[symbol];
      this.$api.claimReward(roundId, resolution, contractName).then(() => {
        $el.setLoading(false);
        this.$api.getBalance();
        this.deleteRow(roundId);
      }).catch(() => {
        $el.setLoading(false);
      })
    },
    deleteRow(epoch) {
      let data = this.data;
      let idx = data.findIndex(({round}) => round.epoch === epoch);
      idx !== -1 && data.splice(idx, 1);
    },
    openBlock(hash) {
      window.open(blockConfig.host + hash);
    },
  },
  beforeMount() {
    this.getList();
  }
}
</script>

<style lang="less" scoped>
.option-record {
  height: 100%;
  cursor: default;
}

.tab-bar {
  padding: 0 25px 20px;
  background-color: #191f2d;
}

.list-box {
  position: relative;
  height: calc(100% - 70px);
}

.m-folder-list {
  height: 100%;
  overflow: auto;
  cursor: default;
}

li {
  color: #b7b7cf;

  &:not(:first-child) {
    margin-top: 3px;
  }
}

.no-data {
  height: 100%;
  line-height: 1.8;
}

.main-info {
  position: relative;
  padding: 0 65px 0 25px;
  height: 80px;
  line-height: 35px;
  background-color: #353b4d;

  .c1 {
    width: 50px;
  }

  .c2 {
    width: 70px;
  }

  .c3 {
    width: 130px;

    .text {
      margin-left: -4px;
    }
  }

  &:hover {
    background-color: #464d65;
  }

  .win-text {
    font-size: 18px;
    width: 50px;
  }

  .arrow {
    position: absolute;
    top: 50%;
    right: 15px;
    transform: translateY(-50%);
  }
}

.detail-info {
  overflow: hidden;
  padding: 0 20px;
  height: 0;

  &.open {
    height: 300px;
  }

  .s1 {
    width: 160px;
  }

  .s3 {
    width: 130px;
  }
}

.base-info {
  padding: 15px 20px 0;
  line-height: 35px;

  .bet-num {
    font-size: 25px;
  }
}

.res-box {
  margin-top: 15px;
  height: 90px;
  padding: 0 18px;
  line-height: 35px;
  border-radius: 10px;
}

footer {
  margin-left: -12px;
  margin-top: 15px;
  padding-bottom: 10px;

  p {
    height: 80px;
    padding-top: 10px;
  }

  .cap {
    margin-right: 10px;
  }

  .res-text {
    margin-right: 20px;
  }

  .gain {
    margin-top: 20px;
  }
}

.block {
  width: 150px;

  img {
    cursor: pointer;
  }
}

@media screen and (max-width: 540px) {
  .m-folder-list {
    font-size: 12px;
  }

  .main-info {
    padding: 0 45px 0 15px;

    .c1 {
      width: 40px;
    }

    .c2 {
      width: 60px;
    }

    .c3 {
      width: 30vw;
    }

    .win-text {
      font-size: 14px;
      width: 44px;
    }

    .arrow {
      right: 5px;
    }
  }

  .detail-info {
    padding: 0 15px;

    &.open {
      height: 230px;
    }

    .s1 {
      width: 33vw;
    }

    .s3 {
      width: 80px;
    }
  }

  .base-info {
    padding: 10px 15px 0;
    line-height: 30px;
  }

  .res-box {
    margin-top: 5px;
    height: 80px;
    padding: 0 13px;
    line-height: 32px;
  }

  footer {
    margin-left: -10px;
    margin-top: 10px;
    padding-bottom: 5px;

    p {
      height: 60px;
      padding-top: 5px;
    }

    .res-text {
      margin-right: 10px;
    }

    .gain {
      margin-top: 17px;
    }

    .icon-l {
      margin-top: 6px;
      height: 40px;
    }

    .cap {
      height: 60px;
      margin-right: 0;
    }
  }

  .block {
    width: 130px;
  }

  .opened .detail-info {
    height: 231px;
  }

  .fs-20 {
    font-size: 16px;
  }
}

::-webkit-scrollbar {
  display: none;
}
</style>