export default {
  namespaced: true,
  state: {
    farmStore: [],
    recordStore: [],
  },
  mutations: {
    setFarmInfo(state, {pid, info}) {
      let s_info = state.farmStore[pid] || {};
      state.farmStore[pid] = {
        ...s_info,
        ...info,
        pid
      };
    },
    setFarmRecord(state, records) {
      let recordStore = [];
      records.map((rec) => {
        let pid =  rec.pool.id;
        if (!recordStore[pid]) recordStore[pid] = [];
        recordStore[pid].push(rec);
      })
      state.recordStore = recordStore;
    },
  },
  actions: {
    setFarmInfo({commit}, params) {
      commit('setFarmInfo', params);
    },
    setFarmRecord({commit}, params) {
      commit('setFarmRecord', params);
    },
  },
}