<template>
  <div class="resolution-counter flex jc-center ai-center flex-column">
    <p class="symbol-text flex jc-center ai-center">
      <img src="../../images/option/time.png" alt="">
      <span>{{ language[item.text] }}</span>
    </p>
    <b v-if="item.online" class="clock text">{{ countDisplay }}</b>
    <span v-else class="clock"></span>
    <i v-if="isCalculating"></i>
  </div>
</template>

<script>
import {counter} from "@/common/util";
import {mapActions, mapState,mapGetters} from "vuex";
import {optionRoundConvertor} from "@/common/dataConvertor";

export default {
  name: "resolutionCounter",
  props: {
    item: {type: Object, require: true}
  },
  data() {
    return {
      timer: null,   //
      countCalculating: true, //
      countTimer: null,   //
      count: 0,   //
      currentEpoch: '',  //epoch
      lockBlock: '',
    }
  },
  watch: {
    currentBlock(nb, ob) {
      if (ob && this.countCalculating) {
        this.startCounter();
        this.countCalculating = false;
      }
    },
    currentEpoch(ne, oe) {
      oe && this.startCounter();
    },
  },
  computed: {
    ...mapState('option',['contractName', 'currentBlock']),
    ...mapState('app',['address']),
    ...mapGetters(['language']),
    resolution() {
      return this.item.type
    },
    isCalculating() {
      let {currentBlock, lockBlock, resolution} = this;
      let isCalculating = currentBlock && lockBlock ? lockBlock <= currentBlock : true;
      this.setCurrentRound({resolution, roundInfo: {isCalculating}});
      return isCalculating;
    },
    countLength(){
      return this.item.type * 60;
    },
    countDisplay() {
      let {resolution, count} = this;
      let countDisplay = this.countCalculating ? '??:??:??' : counter.format(this.count);
      this.setCurrentRound({resolution, roundInfo: {count, countDisplay}});
      return countDisplay;
    },
  },
  methods: {
    ...mapActions('option',['setCurrentRound', 'setCurrentPrice']),
    getCurrentRound() {
      let {resolution,address, contractName} = this;
      this.$api.getCurrentEpoch(resolution, contractName).then(currentEpoch => {
        let p1 = this.$api.getRoundInfo(currentEpoch,resolution, contractName);
        let p2 = address?this.$api.getBetInfoInRound(currentEpoch, address, contractName):Promise.resolve({amount:'0'});
        Promise.all([p1,p2]).then(([roundInfo, {amount}]) => {
          let {lockBlock} = roundInfo = optionRoundConvertor(roundInfo);
          roundInfo.betAmount = amount;
          this.currentEpoch = currentEpoch;
          this.lockBlock = lockBlock;
          this.setCurrentRound({resolution, roundInfo});
        });
      })
    },
    startTimer() {
      if (!this.item.online) return;
      this.getCurrentRound();
      clearInterval(this.timer);
      this.timer = setInterval(() => this.getCurrentRound(), 1000);
    },
    startCounter() {
      clearInterval(this.countTimer);
      let {currentBlock, lockBlock} = this;
      this.count = 0;
      if (!lockBlock) return;
      this.count = counter.calClockCount(currentBlock, lockBlock);
      this.getTimeRange();
      this.countTimer = setInterval(() => {
        this.count > 0 && this.count--;
      }, 1000);
    },
    getTimeRange() {
      let {resolution,countLength,count} = this;
      let t = new Date().getTime() + countLength * 1000;
      let end = new Date(t + count * 1000);
      let s = end.getSeconds();
      end.setTime(t + count * 1000 + (s > 30 ? 60 - s : -s) * 1000);
      let start = new Date(end - countLength * 1000);
      let timeRange = start.format('hh:mm') + ' - ' + end.format('hh:mm');
      this.setCurrentRound({resolution, roundInfo: {timeRange}});
    },
  },
  mounted() {
    this.startTimer();
  },
  unmounted() {
    clearInterval(this.timer);
    clearInterval(this.countTimer);
  }
}
</script>

<style lang="less" scoped>
.resolution-counter {
  height: 60px;
  cursor: pointer;
  line-height: 24px;
  font-size: 16px;
  position: relative;
  .symbol-text {
    text-align: left;
    font-size: 13px;
    img {
      width: 30px;
      margin-left: -18px;
    }
  }
  .clock{
    display: none;
  }
}
</style>