<template>
<div class="record-bar" v-show="titleMap[pathName]" :class="'rec-'+pathName">
  <div class="btn-bar flex ai-center">
    <m-button class="btn mini-hide record hdr-el-blue" @click="openRecord=true">
      <img class="icon" src="../../images/option/history.png" alt=""/><span class="text">{{ language.btn_record }}</span>
    </m-button>
    <img class="icon mini" src="../../images/option/history.png" @click="openRecord=true" alt=""/>
  </div>
  <!--    -->
  <div v-if="openRecord" class="mask fixed" @click.self="openRecord=false"></div>
  <div class="record-slider animate-inout" :class="{open:openRecord}">
    <header class="flex jc-between ai-center">
      <span class="text fs-18">{{ language[titleMap[pathName]] }}</span>
      <img class="btn-close" src="../../images/close@2x.png" @click="openRecord = false" alt=""/>
    </header>
    <div class="record-con" v-if="openRecord">
      <option-record v-if="pathName==='option'"/>
      <farm-record v-if="pathName==='farm'"/>
    </div>
  </div>
</div>
</template>

<script>
import MButton from "@/components/mButton";
import OptionRecord from "@/views/optionComps/optionRecord";
import {mapGetters} from "vuex";
import FarmRecord from "@/views/farm/farmRecord";
export default {
  name: "recordBar",
  components: {FarmRecord, OptionRecord, MButton},
  watch: {
    '$route.path': function () {
      this.openRecord = false;
    },
  },
  computed:{
    ...mapGetters(['language']),
    pathName(){
      return this.$route.name;
    }
  },
  data(){
    return {
      titleMap:{
        option:'bet_record',
        farm:'farm_record',
      },
      openRecord: false,
    }
  },

}
</script>

<style lang="less" scoped>
.btn-bar{
  height: 60px;
}
.mini {
  display: none;
}
.record-slider {
  width: 480px;
  height: 100%;
  position: fixed;
  top: 0;
  right: 0;
  background-color: #232A3D;
  transform: translateX(100%);
  z-index: 9;
  cursor: default;
  &.open {
    transform: translateX(0);
  }
  header {
    height: 80px;
    padding: 0 25px;
    font-size: 14px;
    background-color: #191f2d;
    .btn-close {
      &:hover {
        opacity: .8;
      }
    }
  }
  .record-con{
    height: calc(100% - 80px);
  }
}
@media screen and (max-width: 540px) {
  .record-slider {
    width: 100%;
  }
  .mini {
    display: block;
  }
  .rec-option{
    position: absolute;
    top: 60px;
    right: 10px;
  }
}
</style>