<template>
  <div class="author-bar flex ai-center">
    <div class="wallet animate text flex ai-center relative" @click="togMask()">
      <div class="wallet-icon relative">
        <img class="icon-l d-b" src="../../images/wallet.png" alt="">
        <img v-if="address" class="tag absolute" src="../../images/confirm.png" alt="">
        <img v-else class="tag absolute" src="../../images/fail.png" alt="">
      </div>
      <template v-if="address">
        <span class="addr">{{ addressFormat(address) }}</span>
        <img class="icon-s arrow" src="../../images/option/arrow_down@3x.png" alt="">
      </template>
      <span v-else class="addr d-b" style="padding-right: 15px;">{{ language.tt_3_1 }}</span>
    </div>
    <!--    -->
    <div v-if="showBox" class="mask fixed" @click="showBox = false"></div>
    <div class="switch-box shadow-active animate-inout" :class="{show:showBox&&maskType===0}">
      <header class="flex jc-between ai-center">
        <span class="tt text">{{ language.tt_3_1 }}</span>
        <img class="close" @click="showBox=false" src="../../images/close@2x.png" alt=""/>
      </header>
      <ul>
        <li class="link-item text animate-inout" @click="login(link.type)"
            v-for="link in links" :key="link.name">
          <img :src="link.src" alt=""><span>{{ link.name }}</span>
        </li>
      </ul>
    </div>
    <!--    -->
    <div class="net switch-box shadow-active animate-inout" :class="{show:showBox&&maskType===1}">
      <header class="flex jc-between ai-center">
        <div class="tt text flex ai-center">
          <img class="icon-m" :src="links[walletType].src" alt="">
          {{ links[walletType].name }}
        </div>
        <img class="close" @click="showBox=false" src="../../images/close@2x.png" alt=""/>
      </header>
      <div class="address-all flex ai-center text">{{ address }}</div>
      <ul class="net-list flex jc-between text">
        <li class="label flex ai-center">{{ language.switch_net }}</li>
        <li class="net-item flex ai-center jc-center" v-for="net in netList" :key="net.name"
            :class="{'shadow-active':net.type===1,disabled:net.disabled}"
        >
          {{ net.name }}
        </li>
      </ul>
      <ul class="balance-list">
        <li class="balance-item flex ai-center" v-for="coinName in balanceList" :key="coinName">
          <img class="icon" :src="coinMap[coinName]" alt="bnb">
          <span>{{ coinName }}<br><span class="text fs-16">{{ balanceFormatter(balance[coinName]) }}</span></span>
        </li>
      </ul>
      <div class="btn-group flex jc-between">
        <m-button class="hdr-el-blue text" :height="36" @click="maskType = 0">
          {{ language.switch_wallet }}
        </m-button>
        <m-button class="hdr-el-red red border-red" :height="36" @click="logout">
          {{ language.logout }}
        </m-button>
      </div>
    </div>
  </div>
</template>

<script>

import img_fox from '@/images/fox-logo@3x.png'
import img_im from '@/images/imToken-logo@3x.png'
// import img_tp from '@/images/tp-logo@3x.png'
// import img_wall from '@/images/walletconnect-logo@3x.png'
import {mapState, mapActions, mapGetters} from 'vuex';
import {addressFormat, balanceFormatter} from "@/common/dataConvertor";
import {coinMap} from "@/common/walletConfig";
import MButton from "@/components/mButton";

export default {
  name: 'authorBar',
  components: {MButton},
  data() {
    return {
      walletType: 0,
      showBox: false,
      maskType: 1,
      links: [
        {type: 0, name: 'MetaMask', src: img_fox},
        {type: 1, name: 'imToken', src: img_im},
        // {type: 2, name: 'Token Pocket', src: img_tp},
        // {type: 3, name: 'WalletConnect', src: img_wall},
      ],
      netList: [
        {type: 1, name: 'BSC'},
        {type: 2, name: 'Polygon', disabled: true},
        {type: 3, name: 'Arbitrum', disabled: true},
      ],
      balanceList: ['BNB', 'USDT', 'DFT'],
      timer: null,
      coinMap,
    }
  },
  computed: {
    ...mapState('app', ['address', 'balance']),
    ...mapGetters(['language']),
  },
  watch: {
    '$route.path': function () {
      this.openRecord = false;
    },
    address(address) {
      address && this.getBalance();
    }
  },
  methods: {
    balanceFormatter,
    addressFormat,
    ...mapActions('app', ['setUserInfo']),
    togMask() {
      this.maskType = this.address ? 1 : 0;
      this.showBox = !this.showBox;
    },
    login(type) {
      if (this.currentType === type) return;
      switch (type) {
        case 0:
          this.$api.login().then(() => this.walletType = 0);
          break;
        case 1:
          this.walletType = 1;
          break;
        case 2:
          this.walletType = 2;
          break;
        case 3:
          this.walletType = 3;
          break;
      }
      this.showBox = false;
    },
    getBalance() {
      this.$api.getBalance();
      clearInterval(this.timer);
      this.timer = setInterval(() => {
        this.$api.getBalance();
      }, 5000)
    },
    logout() {
      this.setUserInfo({address: '', balance: 0, defx: 0});
      this.showBox = false;
    },
  },
  mounted() {
    this.$api.login();
  },
  unmounted() {

  }
};
</script>

<style lang="less" scoped>
.mask {
  background-color: transparent;
}
.author-bar {
  height: 60px;
  margin-left: 10px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: relative;
  cursor: default;
  .wallet {
    height: 34px;
    border-radius: 17px;
    padding-left: 40px;
    cursor: pointer;
    background-color: #353B4D;
  }
  .wallet-icon {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(-10%, -50%);
  }
  .tag {
    left: 26%;
    bottom: 26%;
    width: 20%;
  }
  .addr {
    padding-left: 10px;
  }
  .arrow {
    margin-top: 2px;
    margin-right: 3px;
  }
}
.logout {
  cursor: pointer;
  width: 30px;
  margin-left: 20px;
  opacity: .8;
}
.switch-box {
  min-width: 250px;
  padding: 10px 25px 20px;
  border-radius: 10px;
  position: absolute;
  right: 0;
  transform: translate(calc(100% + 90px));
  top: calc(100% + 10px);
  background-color: #191f2d;
  border: 2px solid #FFFFFF;
  &.show {
    transform: translate(0);
  }
  header {
    height: 40px;
    .tt {
      font-size: 16px;
    }
    .icon-m {
      margin-right: 15px;
    }
    .close {
      cursor: pointer;
      &:hover {
        opacity: .8;
      }
    }
  }
  ul {
    margin-top: 10px;
  }
  .link-item {
    font-size: 16px;
    border-radius: 6px;
    background-color: #292D34;
    padding: 5px 0;
    cursor: pointer;
    &:not(:first-child) {
      margin-top: 10px;
    }
    &:hover, &.active {
      background-color: #42424b;
    }
    img {
      width: 40px;
      margin: 0 10px;
      vertical-align: middle;
    }
  }
}
.address-all {
  height: 40px;
  padding-right: 25px;
}
.net-list {
  height: 35px;
  .net-item {
    border-radius: 10px;
    background-color: #292D34;
    min-width: 70px;
    padding: 0 10px;
    cursor: pointer;
    &.disabled {
      cursor: not-allowed;
    }
    &.shadow-active {
      background-color: #353B4D;
    }
  }
}
.balance-list {
  .balance-item {
    height: 60px;
    margin-top: 20px;
    border-radius: 10px;
    background-color: #292D34;
    line-height: 26px;
    cursor: pointer;
    &:not(:first-child) {
      margin-top: 10px;
    }
    img {
      margin: 0 15px;
    }
  }
}
.btn-group {
  margin-top: 20px;
}
@media screen and (min-width: 1024px) {
  .wallet {
    &:hover {
      color: #ccc;
    }
  }
  .logout {
    &:hover {
      opacity: 1;
    }
  }
}

@media screen and (max-width: 540px) {
  .author-bar {
    margin-right: 5px;
  }
  .addr {
    display: none;
  }
  .switch-box {
    padding: 10px 15px 20px;
  }
  .address-all {
    height: 30px;
  }
  .net-list {
    height: 30px;
    .net-item {
      border-radius: 10px;
      background-color: #292D34;
      min-width: 65px;
      padding: 0 5px;
      cursor: pointer;
      &.disabled {
        cursor: not-allowed;
      }
      &.shadow-active {
        background-color: #353B4D;
      }
    }
  }
  .balance-list {
    .balance-item {
      height: 50px;
      margin-top: 20px;
      line-height: 20px;
      cursor: pointer;
      &:not(:first-child) {
        margin-top: 10px;
      }
      img {
        margin: 0 15px;
      }
    }
  }
}
@media screen and (max-width: 420px) {
  .author-bar{
    .wallet{
      height: 26px;
      padding-left: 30px;
    }
  }
  .wallet-icon{
    .icon-l{
      height: 40px;
    }
  }
}
</style>